import React from 'react'
import { Panel, PanelHeader, List, SimpleCell } from '@vkontakte/vkui'

const Groups = ({ id, state, openCatalog }) => (
  <Panel id={id}>
    <PanelHeader>Сообщества</PanelHeader>
    <List>
      {
        state.catalog && (
          state.catalog.map((item, index) => 
            <SimpleCell
              key={item.catalog}
              id={index}
              expandable
              onClick={openCatalog}
            >
              {item.catalog}
            </SimpleCell>
          )
        )
      }
    </List>
  </Panel>
)

export default Groups
