import React, { Component } from 'react'
import { Panel, PanelHeader, FormLayout, Header, Input, Select, Checkbox, Button, Div, RangeSlider } from '@vkontakte/vkui'
import Icon28AddCircleOutline from '@vkontakte/icons/dist/28/add_circle_outline';
import Icon28CancelCircleOutline from '@vkontakte/icons/dist/28/cancel_circle_outline';

const SEASONS_TRANSCRIPTION = {
  'all': 'любое',
  'winter': 'зима',
  'spring': 'весна',
  'summer': 'лето',
  'fall': 'осень',
};

const MONTHS_TRANSCRIPTION = {
  0: 'любой',
  1: 'январь', 2: 'февраль', 3: 'март', 4: 'апрель',
  5: 'май', 6: 'июнь', 7: 'июль', 8: 'август',
  9: 'сентябрь', 10: 'октябрь', 11: 'ноябрь', 12: 'декабрь',
};

const LANGUAGES = {
  de: "немецкий",
  en: "английский",
  es: "испанский",
  fr: "французский",
  it: "итальянский",
  ru: "русский",
  th: "тайский",
  tr: "турецкий"
}

const CURRENCY = {
  AZN: "манат",
  BYN: "беларусский рубль",
  CNY: "юань",
  EUR: "евро",
  KGS: "сом",
  KZT: "тенге ",
  RUB: "рубль ",
  THB: "бат",
  UAH: "гривна",
  USD: "доллар",
  UZS: "сум"
}

const PASSENGERS = {
  url: "1 пассажир, экономкласс",
  url1Business: "1 пассажир, бизнес-класс",
  url2Passengers: "2 пассажира, экономкласс",
  urlForma: "открытая редактируемая форма поиска"
}

export class Settings extends Component {
  state = {
    updated: "60",
    limit: "0",
    datesCnt: [{
      yearSrc: (new Date()).getFullYear(),
      seasonSrc: SEASONS_TRANSCRIPTION['all'],
      monthSrc: MONTHS_TRANSCRIPTION['0']
    }],
    language: LANGUAGES['ru'],
    currency: CURRENCY['RUB'],
    currencyForUrl: CURRENCY['RUB'],
    costMin: 100,
    costMax: 500000
  }

  onChange = e => {
    this.setState({
      [e.currentTarget.name]: e.currentTarget.value
    })
  }

  onChangeFrom = e => {
    const { datesCnt } = this.state
    datesCnt[parseInt(e.currentTarget.id, 10)][e.currentTarget.name] = e.currentTarget.value
    this.setState({
      datesCnt
    })
  }

  addFrom = () => {
    const { datesCnt } = this.state
    datesCnt.push({
      yearSrc: (new Date()).getFullYear(),
      seasonSrc: SEASONS_TRANSCRIPTION['all'],
      monthSrc: MONTHS_TRANSCRIPTION['0']
    })
    this.setState({
      datesCnt
    })
  }

  removeFrom = () => {
    const { datesCnt } = this.state
    this.setState({
      datesCnt: datesCnt.splice(0, datesCnt.length-1)
    })
  }

  choose = (name, data) => {
    console.log(name,data);
    this.setState({ [name]: data })
  }

  render() {
    const {
      id
    } = this.props
    const {
      datesCnt,
      currency,
      currencyForUrl,
      language,
      countrySrc,
      citySrc,
      countryDst,
      cityDst,
      continentDst,
      randomCost,
      costMin,
      costMax
    } = this.state
    return (
      <Panel id={id}>
        <PanelHeader>Настройки</PanelHeader>
        <FormLayout>
          <Header>Откуда</Header>
          <Input
            top="Страна вылета"
            name="countrySrc"
            onClick={() => {
              this.props.changeDefaultName("countrySrc")
              this.props.changeFunction(this.choose)
              this.props.changeDefault("Страна вылета")
              this.props.changeDefaultText(countrySrc)
              this.props.go("chooseCountry");
            }}
            value={countrySrc}
          />
          <Input
            top="Город вылета"
            name="citySrc"
            onChange={this.onChange}
            value={citySrc}
          />
          <Header>Куда</Header>
          <Input
            top="Часть света"
            name="continentDst"
            onChange={this.onChange}
            value={continentDst}
          />
          <Input
            top="Страна"
            name="countryDst"
            onChange={this.onChange}
            value={countryDst}
          />
          <Input
            top="Город"
            name="cityDst"
            onChange={this.onChange}
            value={cityDst}
          />
          <Header>Интервал</Header>
          <Select
            top="Получать дешевые авиабилеты один раз в:"
            name="interval"
            placeholder="Интервал"
          >
            <option value="60">Час</option>
            <option value="240">Четыре часа</option>
            <option value="480">Восемь часов</option>
            <option value="1440">24 часа</option>
          </Select>
          <Select
            top="Получать информацию за крайние N, часов"
            name="updated"
            defaultValue="60"
          >
            <option value="60">1 час</option>
          </Select>
          <Header>Количество</Header>
          <Select
            top="Сколько дешевых авиабилетов показывать в сутки?"
            name="limit"
            defaultValue="0"
          >
            <option value="0">Все</option>
            <option value="20">20</option>
            <option value="50">50</option>
            <option value="100">100</option>
            <option value="300">300</option>
          </Select>
          <Header>Полеты</Header>
          <Checkbox name="forward" onChange={this.onChange}>Туда</Checkbox>
          <Checkbox name="dual" onChange={this.onChange}>Туда-обратно</Checkbox>
          <Header>Когда</Header>
          {
            datesCnt && (
              datesCnt.map((item, i) => 
                <FormLayout key={i}>
                  <Select
                    top={`Год вылета (${i+1})`}
                    name="yearSrc"
                    id={i}
                    onChange={this.onChangeFrom}
                    value={item.yearSrc}
                  >
                    {
                      Array.from(Array(31).keys()).map((year) => 
                        <option key={year} vaule={(new Date()).getFullYear()+year}>
                          {(new Date()).getFullYear()+year}
                        </option>
                      )
                    }
                  </Select>
                  <Select
                    top={`Время года (${i+1})`}
                    name="seasonSrc"
                    id={i}
                    onChange={this.onChangeFrom}
                    value={item.seasonSrc}
                  >
                    {
                      Object.keys(SEASONS_TRANSCRIPTION).map((season) => 
                        <option value={season} key={season}>
                          {SEASONS_TRANSCRIPTION[season]}
                        </option>
                      )
                    }
                  </Select>
                  <Select
                    top={`Месяц года (${i+1})`}
                    name="monthSrc"
                    id={i}
                    onChange={this.onChangeFrom}
                    value={item.monthSrc}
                  >
                    {
                      Object.keys(MONTHS_TRANSCRIPTION).map((month) => 
                        <option value={month} key={month}>
                          {MONTHS_TRANSCRIPTION[month]}
                        </option>
                      )
                    }
                  </Select>
                </FormLayout>
              )
            )
          }
          {
            datesCnt.length > 1
          }
          <Div style={{ display: 'flex' }}>
            {
              datesCnt.length < 3 && (
                <Button
                  size="l"
                  onClick={this.addFrom}
                >
                  <Icon28AddCircleOutline />
                </Button>
              )
            }
            {
              datesCnt.length > 1 && (
                <Button
                  size="l"
                  mode="destructive"
                  onClick={this.removeFrom}
                >
                  <Icon28CancelCircleOutline color="rgb(255, 26, 0)" />
                </Button>
              )
            }
          </Div>
          <Select
            top="Язык"
            name="language"
            onChange={this.onChange}
            value={language}
          >
            {
              Object.keys(LANGUAGES).map((language) => 
                <option value={language} key={language}>
                  {LANGUAGES[language]}
                </option>
              )
            }
          </Select>
          <Select
            top="Валюта"
            name="currency"
            onChange={this.onChange}
            value={currency}
          >
            {
              Object.keys(CURRENCY).map((currency) => 
                <option value={currency} key={currency}>
                  {CURRENCY[currency]}
                </option>
              )
            }
          </Select>
          <Select
            top="Валюта метапоисковика"
            name="currencyForUrl"
            onChange={this.onChange}
            value={currencyForUrl}
          >
            {
              Object.keys(CURRENCY).map((currency) => 
                <option value={currency} key={currency}>
                  {CURRENCY[currency]}
                </option>
              )
            }
          </Select>
          <Select
            top="Пассажиры"
            name="currencyForUrl"
            onChange={this.onChange}
          >
            {
              Object.keys(PASSENGERS  ).map((passengers) => 
                <option value={passengers} key={passengers}>
                  {PASSENGERS[passengers]}
                </option>
              )
            }
          </Select>
          <Header>Стоимость, {currency}</Header>
          <Checkbox
            checked={randomCost}
            onChange={(e) => {
              this.setState({
                randomCost: e.currentTarget.checked
              })
            }}
          >
            Любая
          </Checkbox>
          <RangeSlider
            top={costMin + " - " + costMax}
            min={100}
            max={500000}
            step={100}
            defaultValue={[100, 500000]}
            value={[costMin, costMax]}
            onChange={(value) => {
              this.setState({
                costMin: value[0],
                costMax: value[1],
                randomCost: false
              })
            }}
          />
          <Div style={{ display: 'flex' }}>
            <Input
              onFocus={() => {
                console.log('focus')
              }}
              value={costMin}
              type="number"
              onChange={(e) => {
                const value = parseInt(e.currentTarget.value, 10)
                if(value){
                  if(value < costMax && value > 100){
                    this.setState({ costMin: value })
                  }
                }
              }}
            />
            <Input
              value={costMax}
              type="number"
              onChange={(e) => {
                const value = parseInt(e.currentTarget.value, 10)
                if(value){
                  if(value > costMin && value < 500000){
                    this.setState({ costMax: value })
                  }
                }
              }}
            />
          </Div>
        </FormLayout>
      </Panel>
    )
  }
}

export default Settings 
