import React, { Component } from 'react'
import { Panel, PanelHeader, PanelHeaderBack, RichCell, SimpleCell, Link, Button, Alert, ScreenSpinner } from '@vkontakte/vkui'
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import bridge from '@vkontakte/vk-bridge'
import SubscribeCities from '../components/SubscribeCities'

export class Catalog extends Component {
  // Подписка на сообщество
  subscribe = e => {
    const screenName = e.currentTarget.id.split("com/")[1]
    const { state } = this.props
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: state.appId,
      scope: "wall,groups",
      request_id: 1
    })
    .then(auth => {
      if(auth.access_token && auth.scope === "wall,groups"){
        bridge.sendPromise("VKWebAppCallAPIMethod", {
          method: "groups.getById",
          request_id: 2,
          params: {
            group_id: screenName,
            access_token: auth.access_token,
            v: 5.107,
          }
        })
        .then(data => {
          bridge.sendPromise("VKWebAppJoinGroup", {"group_id": data.response[0].id})
          .then(() => {
            // Если пользователь подписался, то отправляем запрос 
            // на сохранение этих данных
            fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/save-groups", {
              "headers": {
                "accept": "*/*",
                "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
                "content-type": "application/json;charset=utf-8",
              },
              "body": JSON.stringify({
                user_id: state.user.id,
                app_id: state.appId,
                checkbox: data.response[0].id
              }),
              "method": "POST",
            });
          })
        })
      }
    })
  }

  // Запрос на сообщения
  subscribeMessages = (screenName, name) => {
    const { state } = this.props
    this.props.setPopout(<ScreenSpinner />)
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: state.appId,
      scope: "wall,groups",
      request_id: 1
    })
    .then(auth => {
      if(auth.access_token && auth.scope === "wall,groups"){
        bridge.sendPromise("VKWebAppCallAPIMethod", {
          method: "groups.getById",
          request_id: 2,
          params: {
            group_id: screenName,
            access_token: auth.access_token,
            v: 5.107,
          }
        })
        .then(data => {
          bridge.sendPromise("VKWebAppAllowMessagesFromGroup", {"group_id": data.response[0].id, "key": "dBuBKe1kFcdemzB"})
          .then(() => {
            bridge.sendPromise("VKWebAppCallAPIMethod", {
              method: "wall.get",
              request_id: 2,
              params: {
                domain: screenName,
                access_token: auth.access_token,
                v: 5.107,
              }
            })
            .then(async tags => {
              let tagsIn = [];
              let tagsOut = [];
              let itemsWall = tags.response.items;
              itemsWall.forEach((group) => {
                let match = [...group.text.matchAll(/(#.*@.*)/g)];
                if (match.length) {
                  if (match.length > 2) {
                    let matchOut = match[2];
                    tagsOut.push(matchOut)
                }
                let matchIn = match[0];
                tagsIn.push(matchIn)
                }
              });
              tagsIn = tagsIn.map((item) => 
                item[0]
              )
              tagsIn = tagsIn.filter((v,i) => tagsIn.indexOf(v) === i)
              tagsOut = tagsOut.map((item) => 
                item[0]
              )
              tagsOut = tagsOut.filter((v,i) => tagsOut.indexOf(v) === i)
              this.props.setPopout(
                <SubscribeCities
                  userId={state.user.id}
                  section={state.currentSection}
                  catalog={state.catalog[state.currentCatalog].catalog}
                  name={name}
                  groupId={data.response[0].id}
                  from={tagsOut.map((item) => {
                    return {
                      tag: item,
                      checked: false
                    }
                  })}
                  to={tagsIn.map((item) => {
                    return {
                      tag: item,
                      checked: false
                    }
                  })}
                  close={() =>this.props.setPopout(null)}
                />
              )
            })
            .catch(() => this.props.setPopout(null))
          })
          .catch(() => this.props.setPopout(null))
        })
        .catch(() => this.props.setPopout(null))
      }
    })
    .catch(() => this.props.setPopout(null))
  }

  // Предупреждение с информацией
  subscribeMessagesAlert = e => {
    const screenName = e.currentTarget.id.split("com/")[1]
    const name = e.currentTarget.name
    this.props.setPopout(
      <Alert
        actions={[{
          title: 'Отмена',
          autoclose: true,
          mode: 'cancel'
        }, {
          title: 'Разрешить',
          autoclose: true,
          action: () => this.subscribeMessages(screenName, name),
        }]}
        onClose={() => this.props.setPopout(null)}
      >
        <p>Вы можете получать со стены сообщества в личные сообщения только те авиабилеты, которые Вам интересны. Для этого разрешите сейчас сообщения в сообществе и выберите города прилета из списка.</p>
      </Alert>
    )
  }

  render() {
    const {
      id,
      state,
      go,
    } = this.props
    return (
      <Panel id={id}>
        <PanelHeader
          left={
            <PanelHeaderBack onClick={() => go("home")} />
          }
        >{state.catalog[state.currentCatalog].catalog}</PanelHeader>
        {
          state.catalog && (
            state.catalog[state.currentCatalog].to.map((item, index) => 
              item.section ? 
                <SimpleCell
                  key={index}
                  expandable
                  id={index}
                  onClick={this.props.chooseSection}
                >
                  <Link>{item.section}</Link>
                </SimpleCell>
              :
              <RichCell
                key={index}
                disabled
                multiline
                actions={
                  <React.Fragment>
                    <Button
                      mode="outline"
                      id={item.channelName}
                      onClick={this.subscribe}
                    >Подписаться</Button>
                     <Button
                      mode="outline"
                      id={item.channelName}
                      onClick={e => window.open(e.currentTarget.id, '_blank')}
                    >Посмотреть</Button>
                    <Link name={item.description} id={item.channelName} onClick={this.subscribeMessagesAlert}>
                      <Icon28SettingsOutline />
                    </Link>
                  </React.Fragment>
                }
              >
                {item.description}
              </RichCell>
            )
          )
        }
      </Panel>
    )
  }
}

export default Catalog
