import React, { Component } from 'react'
import { Panel, PanelHeader, FixedLayout, Tabs, TabsItem } from '@vkontakte/vkui'
import MyGroups from '../components/MyGroups'
import MyDB from '../components/MyDB'

export class MySubs extends Component {
  state = {
    activeTab: 'groups',
  }

  render() {
    const {
      activeTab
    } = this.state
    const {
      id,
      state
    } = this.props
    return (
      <Panel id={id}>
        <PanelHeader>Мои подписки</PanelHeader>
        <FixedLayout vertical="top">
          <Tabs>
            <TabsItem
              selected={activeTab === 'groups'}
              onClick={() => this.setState({ activeTab: 'groups' })}
            >Сообщества</TabsItem>
            <TabsItem
              selected={activeTab === 'database'}
              onClick={() => this.setState({ activeTab: 'database' })}
            >База данных</TabsItem>
          </Tabs>
        </FixedLayout>
        <div style={{ marginTop: '60px' }} />
        {
          activeTab === 'groups' ? 
            <MyGroups
              setPopout={this.props.setPopout}
              state={state}
            />
          :
            <MyDB
              setPopout={this.props.setPopout}
              state={state}
            />
        }
      </Panel>
    )
  }
}

export default MySubs
