import React, { Component } from 'react'
import { Panel, PanelHeader, Placeholder, Button, RichCell, Avatar, ScreenSpinner } from '@vkontakte/vkui'
import Icon56UsersOutline from '@vkontakte/icons/dist/56/users_outline';
import bridge from '@vkontakte/vk-bridge'

export class Friends extends Component {
  state = {
    friends: null,
  }

  componentDidMount() {
    this.getFriends()
  }
  
  getFriends = () => {
    this.props.setPopout(<ScreenSpinner />)
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: this.props.state.appId,
      scope: "friends"
    })
    .then(data => {
      if(data.access_token){
        if(data.scope === "friends"){
          bridge.sendPromise("VKWebAppCallAPIMethod", {
            method: "friends.get",
            params: {
              access_token: data.access_token,
              fields: "city,photo_50",
              v: 5.107
            },
            request_id: '3'
          })
          .then(data => {
            if(data.response.items){
              this.setState({
                friends: data.response.items.filter((item) => item.city).filter((item) => 
                  item.city.id !== this.props.state.user.city.id
                )
              })
              this.props.setPopout(null)
            }else{
              this.props.setPopout(null)
            }
          })
          .catch(() => this.props.setPopout(null))
        }else{
          this.props.setPopout(null)
        }
      }else{
        this.props.setPopout(null)
      }
    })
    .catch(() => this.props.setPopout(null))
  }

  getPrice = (e) => {
    this.props.setPopout(<ScreenSpinner />)
    const city = e.currentTarget.id.split("_")[1]
    const id = e.currentTarget.id.split("_")[0]
    fetch("https://cors-anywhere.herokuapp.com/https://api.travelpayouts.com/data/ru/cities.json")
    .then(response => response.json())
    .then(cities => {
      const myCode = cities.filter((item) => item.name === this.props.state.user.city.title)[0].code
      const friendCode = cities.filter((item) => item.name === city)[0].code
      fetch(`https://cors-anywhere.herokuapp.com/https://api.travelpayouts.com/v1/prices/calendar?&origin=${myCode}&destination=${friendCode}&calendar_type=departure_date&token=d378bb3f3b879e6fc87899314ba5ce5d`)
      .then(response => response.json())
      .then(data => {
        let indexPrice = Object.keys(data.data)[0]
        if(Object.keys(data.data).length > 3){
          indexPrice = Object.keys(data.data)[3]
        }
        this.setState({
          friends: this.state.friends.map((item) => {
            if(item.id === parseInt(id, 10)){
              item.price = (data.data[indexPrice].price + " " + data.currency)
            }
            return item
          })
        })
        this.props.setPopout(null)
      })
    })
  }

  render() {
    const {
      id
    } = this.props
    const {
      friends
    } = this.state
    return (
      <Panel id={id}>
        <PanelHeader>Друзья</PanelHeader>
        {
          friends ? 
          friends.map((item) => 
            <RichCell
              key={item.id}
              actions={
                <React.Fragment>
                  <Button
                    id={item.id+"_"+item.city.title}
                    onClick={this.getPrice}
                  >
                    {
                      item.price ? item.price : "Показать цену"
                    }
                  </Button>
                </React.Fragment>
              }
              text={item.city.title}
              before={<Avatar src={item.photo_50}/>}
            >
              {item.first_name+" "+item.last_name}
            </RichCell>
          )
          :
          <Placeholder
            icon={<Icon56UsersOutline />}
            header="Друзья"
            action={<Button onClick={this.getFriends} size="l">Показать список</Button>}
          >
            Чтобы получить список к друзьям, вам нужно предоставить доступ приложению.
          </Placeholder>
        }
      </Panel>
    )
  }
}

export default Friends
