import React, { Component } from 'react'
import { Panel, PanelHeader, PanelHeaderBack, Div, Input, List, SimpleCell } from '@vkontakte/vkui'
import Icon24Done from '@vkontakte/icons/dist/24/done';

export class ChooseCountry extends Component {
  state = {
    inp: this.props.default
  }

  onChange = (e) => {
    fetch(`https://autocomplete.travelpayouts.com/places2?term=${
      e.currentTarget.value
    }&locale=ru`)
    .then(response => response.json())
    .then(list => {
      if(list.length > 0){
        var resArr = [];
        list.forEach(function(item){
          var i = resArr.findIndex(x => x.country_name === item.country_name);
          if(i <= -1){
            resArr.push(item);
          }
        });
        this.setState({
          list: resArr
        })
      }
    })
  }

  onChoose = (country_name) => {
    this.props.changeFunction(this.props.changeDefaultName,country_name)
    this.props.back()
  }

  render() {
    const {
      list
    } = this.state
    const {
      id,
      back,
      text
    } = this.props
    return (
      <Panel id={id}>
        <PanelHeader
          left={
            <PanelHeaderBack onClick={back} />
          }
        >{text}</PanelHeader>
        <Div>
          <Input
            onChange={this.onChange}
          />
        </Div>
        <List>
          {
            list && list.map((item) => 
              <SimpleCell
                onClick={this.onChoose}
                id={item.country_name}
                after={
                  item.country_name === text && (
                    <Icon24Done />
                  )
                }
              >
                {item.country_name}
              </SimpleCell>
            )
          }
        </List>
      </Panel>
    )
  }
}

export default ChooseCountry
